.Navigation {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.5);
  top: 0;
  width: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0px 40px;
  z-index: 1;
}

.nav-item {
  font-size: 1.1em;
  font-weight: bold;
  text-transform: uppercase;
  margin: 10px 25px;
}

.nav-link {
  color: rgb(10, 13, 66);
}



